import "./index.css";
import { Config } from "./config";
import { MDCLinearProgress } from "@material/linear-progress";
import LoadingHTML from "./loading";
import times from "./times";
import getChallenge from "./utils";

class ContinenteCredentials {
  containerSelector = undefined;
  clientId = undefined;
  container = undefined;
  onClose = undefined;
  onSuccess = undefined;
  loadingContainer = undefined;
  isMobile = false;
  baseUrl = undefined;
  modalContainerId = "continente-credentials-container-modal";
  containerId = "continente-credentials-container";

  constructor(
    containerSelector,
    clientId,
    {
      baseUrl = Config.continenteCredentialsUrl,
      onClose,
      onSuccess,
      isMobile = false,
    } = {}
  ) {
    if (!clientId) {
      throw new Error("clientId is required");
    }

    if (!onSuccess) {
      throw new Error("onSuccess is required");
    }

    this.containerSelector = containerSelector;
    this.clientId = clientId;
    this.onClose = onClose;
    this.onSuccess = onSuccess;
    this.baseUrl = baseUrl;
    this.isMobile = isMobile;

    window.onmessage = this.onMessage.bind(this);
  }

  open() {
    this.createContainer();
  }

  close() {
    if (this.container) {
      this.container.remove();
    }

    if (this.onClose) {
      this.onClose();
    }
  }

  createHeader(container) {
    const header = document.createElement("div");
    header.className = "header";
    header.style.width = `${container.clientWidth}px`;

    this.loadingContainer = document.createElement("div");
    this.loadingContainer.innerHTML = LoadingHTML;
    const loading = new MDCLinearProgress(this.loadingContainer);
    loading.determinate = false;

    header.appendChild(this.loadingContainer);

    return header;
  }

  createIframe() {
    return getChallenge()
      .then((challenge) => {
        var urlIframe = `${this.baseUrl}/user-register?clientId=${
          this.clientId
        }&close=${!this.isMobile}`;

        var url = new URL(urlIframe);
        url.searchParams.append("clientId", this.clientId);
        url.searchParams.append("code_challenge", challenge);
        url.searchParams.append("code_challenge_method", "S256");

        const iframe = document.createElement("iframe");
        iframe.src = url;
        iframe.sandbox =
          "allow-same-origin allow-scripts allow-popups allow-forms";
        iframe.addEventListener("load", () => {
          this.loadingContainer.remove();
        });

        return iframe;
      })
      .catch((e) => {
        console.log(e);
        return null;
      });
  }

  createCloseButton() {
    const closeButton = document.createElement("div");

    closeButton.className = "close-button";
    closeButton.innerHTML = times;

    closeButton.addEventListener("click", () => {
      this.close();
    });

    return closeButton;
  }

  createContainer() {
    if (!document.body) {
      return;
    }

    this.container = document.createElement("div");
    this.container.className = this.containerId;

    const divContainer = document.querySelector(this.containerSelector);

    const header = this.createHeader(divContainer);
    const closeButton = this.createCloseButton();

    //const iframe = this.createIframe();
    this.createIframe().then((iframeResult) => {
      this.container.appendChild(header);
      this.container.appendChild(closeButton);
      this.container.appendChild(iframeResult);

      if (divContainer) {
        const closeButtonRemove = this.container.querySelector(".close-button");
        if (closeButtonRemove) {
          closeButtonRemove.remove();
        }

        divContainer.appendChild(this.container);
      } else {
        this.container.className = this.modalContainerId;
        document.body.appendChild(this.container);
      }
    });
  }

  onMessage(e) {
    if (e.origin !== this.baseUrl) {
      return;
    }

    switch (e.data.type) {
      case "close":
        this.close();
        break;
      case "signup":
        window.location.href = e.data.redirect_url;
        break;
      case "login":
        this.onSuccess(e.data);
        break;
      case "success":
        this.onSuccess(e.data);
        break;
    }
  }
}

export default ContinenteCredentials;
